.pList {
	width: 100%;
	max-width: 1024px;
	display: flex;
	justify-content: space-between;
	gap: 20px;
}

.pListItem {
	border-radius: 10px;
	overflow: hidden;
	cursor: pointer;
	flex: 1;
}

.pListImg {
	width: 100%;
	object-fit: cover;
	height: 150px;
}

.pListTitles > h1 {
	font-size: 18px;
}
.pListTitles > h2 {
	font-size: 14px;
	font-weight: 300;
}
