.mail {
	width: 100%;
	margin-top: 50px;
	background-color: #003580;
	color: white;
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 20px;
	padding: 50px;
}

.mailInputContainer > Input {
	width: 300px;
	height: 30px;
	padding: 10px;
	border: none;
	margin-right: 10px;
	outline: none;
}

.mailInputContainer > button {
	height: 50px;
	background-color: #0071c2;
	color: white;
	font-weight: 500;
	border: none;
	border-radius: 5px;
	cursor: pointer;
}
